<template>
  <div>
    <el-card class="box-card">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="searchmodel" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="searchByName"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addFormVisible=true">添加模块</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="modelList"
        border
        style="width: 100%"
        :stripe="true"
        v-loading="tableLoading"
      >
        <el-table-column fixed prop="id" label="ID" width="100" align="center"></el-table-column>
        <el-table-column prop="item_name" label="名称" width="200" align="center"></el-table-column>
        <el-table-column prop="item_des" label="描述" width="250" align="center"></el-table-column>
        <el-table-column prop="create_name" label="创建者账号" width="200" align="center"></el-table-column>
        <el-table-column prop="create_time" label="创建时间" width="300" align="center"></el-table-column>
        <el-table-column prop="change_time" label="改变时间" width="300" align="center"></el-table-column>
        <el-table-column prop="status" label="状态" width="80" align="center">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="statusChange(scope.row.status,scope.row.id)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="180" align="center">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="编辑" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="showUpdateModel(scope.row.id)"
              ></el-button>
            </el-tooltip>

            <el-tooltip class="item" effect="dark" content="删除" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle @click="delModel(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

<!-- 添加用户界面 -->
    <el-dialog title="添加模块" :visible.sync="addFormVisible" width="50%" @close="addclose">
      <el-form :model="addform"  ref="addFormRef">
        <el-form-item label="名称" :label-width="formLabelWidth" >
          <el-input v-model="addform.name" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item label="描述" :label-width="formLabelWidth" >
          <el-input v-model="addform.desc" autocomplete="off" size="large"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addModel()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改用户界面 -->
    <!-- <el-dialog title="修改用户" :visible.sync="updateFormVisible" width="50%" @close="editclose" >
      <el-form :model="updateform" ref="updateFormRef" :rules="addrule" v-loading="userLoading">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input
            :value="updateform.username"
            autocomplete="off"
            size="large"
            disabled="disabled"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth" prop="password">
          <el-input v-model="updateform.password" autocomplete="off" size="large"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" :label-width="formLabelWidth" prop="email">
          <el-input v-model="updateform.email" autocomplete="off" size="large"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser()">确 定</el-button>
      </div>
    </el-dialog>-->
  </div>
</template>
<script>
import { postRequest } from "../utils/api";
import { getRequest } from "../utils/api";
export default {
  data() {
    return {
      url: "ws://localhost//productWebSocket/1",
      socket: "",
      searchmodel: "",
      tableLoading: true,
      addFormVisible:false,
      formLabelWidth:"120",
      modelList: [],
      addform:{
        name:"",
        desc:""
      },
    };
  },
  mounted() {
    // this.init();
    this.loadModelList();
  },
  destroyed() {
    this.socket.close();
  },
  methods: {
    init() {
      this.socket = new WebSocket(this.url);
      this.socket.onopen = this.open;
      this.socket.onclose = this.close;
    },
    open() {
      console.log("sokect open");
    },
    close() {
      console.log("sokect close");
    },
    send(params) {
      this.socket.send(params);
    },
    loadModelList() {
      getRequest("/items").then(
        resp => {
          this.modelList = resp.data.data;
          console.log(this.modelList)
          this.tableLoading = false;
        },
        resp => {
          alert("加载用户失败");
        }
      );
    },
    searchByName() {},
    statusChange(id) {},
    showUpdateModel(id) {},
    delModel(id) {},
    addModel(){},
    addclose(){},
  }
};
</script>
